.add-task {
	.form-task {
		display: flex;
		align-items: center;
		margin-bottom: 24px;
	}

	.task-massage {
		flex: 1 1 auto;
		-webkit-appearance: none; /* ← Этот хак нужен сафари для работы (проблема со свойством height)... */
		height: 42px;
		padding: 0 14px;
		margin-right: 16px;
		caret-color: #3b8ef3;
		border-radius: 4px;
		border-color: #6c757d;
		outline: none;
		transition: border-color 0.3s ease-in-out;
		background-color: #fff;
		font-size: 20px;
		text-overflow: ellipsis;

		&::placeholder {
			font-size: 20px;
			transition: color 0.1s ease-in-out;
			color: #6c757d;
		}

		&:hover,
		&:focus {
			border-color: #3b8ef3;

			&::placeholder {
				color: transparent;
			}
		}
	}

	.task-btn {
		position: relative;
		display: inline-flex;
		box-sizing: border-box;
		align-items: center;
		justify-content: center;
		padding: 8px;
		min-height: 40px;
		border-radius: 4px;
		outline: none;
		background-color: #0d6efd;
		-webkit-appearance: none;
		border: none;
		text-decoration: none !important;
		text-align: center;
		vertical-align: middle;
		cursor: pointer;
		user-select: none;
		color: #fff;
		transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

		&:hover,
		&:focus {
			color: #fff;
			background-color: rgba(#0b5ed7,0.5);
		}
	}
}